<template>
  <div class="app__navigation">
    <div class="app__navigation-offset"></div>
    <div class="app__navigation-menu">
      <div
        v-show="isFocus"
        class="app__navigation-item--select"
        :style="{ left: `${focusLeft}px` }"
      ></div>
      <button
        v-for="item in items"
        :ref="'item-' + item.id"
        :key="item.id"
        class="app__navigation-item"
        :class="{
          'app__navigation-item--selected': item.id === selectedItem,
          'app__navigation-item--focused': item.id === focusedItem,
        }"
        @focus="(e) => focusItem(e.target, item.id)"
        @mousemove="(e) => focusItem(e.target, item.id)"
        @blur="() => blurItem(item.id)"
        @click="() => selectItem(item)"
      >
        {{ item.name }}
      </button>
    </div>
  </div>
</template>
<script>
import screens, { defaultScreen } from "@/config/screens";

export default {
  name: "float-navigation",
  data() {
    return {
      isFocus: true,
      focusLeft: 0,
      focusedItem: defaultScreen,
      selectedItem: defaultScreen,
      items: screens,
    };
  },
  mounted() {
    this.focusItem(this.$refs["item-" + this.focusedItem][0], this.focusedItem);
  },
  methods: {
    focusItem(e, id) {
      this.focusLeft = e.offsetLeft || 0;
      this.focusedItem = id;
      this.isFocus = true;
    },
    selectItem(item) {
      if (!item.action) {
        this.selectedItem = item.id;
        this.$emit("change", item.id);
      } else {
        this.$emit("action", item.id);
      }
    },
    blurItem(id) {
      setTimeout(() => {
        if (this.focusedItem === id) {
          this.focusedItem = null;
          this.isFocus = false;
        }
      }, 100);
    },
  },
};
</script>
<style lang="scss">
.app__navigation {
  position: relative;
  display: flex;
  align-items: center;
  position: absolute;
  width: 100%;
  top: 32px;
  &-offset {
    width: 100%;
    height: 80px;
  }
  &-menu {
    position: absolute;
    top: 0px;
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0%);
    margin: auto;
    background: rgba(0, 0, 0, 0.4);
    border-radius: 40px;
    display: flex;
  }
  &-item {
    font-size: 20px;
    font-weight: 600;
    color: rgba(255, 255, 255, 0.6);
    background: none;
    border: none;
    outline: none;
    width: 182px;
    padding: 8px;
    z-index: 10;
    transition: 0.2s;
    &--selected {
      color: white;
    }
    &--focused {
      color: rgba(28, 34, 38, 1);
    }
    &--select {
      position: absolute;
      background-color: white;
      left: 0px;
      top: -4px;
      height: 48px;
      width: 182px;
      border-radius: 30px;
      transition: 0.2s;
    }
  }
}
</style>
